*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 430px;
  padding: 0 20px;
  font-family: "Space Grotesk", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: black;
  font-weight: 400;
}
@media ((min-width: 1440px)) {
  .container {
    width: 1440px;
    padding: 0 110px;
    font-size: 18px;
    line-height: 27px;
    color: black;
    font-weight: 400;
  }
}

@font-face {
  font-family: "Space Grotesk";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("../../fonts/SpaceGrotesk-Regular.woff2") format("woff2"), url("../../fonts/SpaceGrotesk-Regular.woff") format("woff");
}
@font-face {
  font-family: "Space Grotesk";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("../../fonts/SpaceGrotesk-Medium.woff2") format("woff2"), url("../../fonts/SpaceGrotesk-Medium.woff") format("woff");
}
.button {
  width: 21.6667rem;
  height: 3.7778rem;
  color: white;
  background-color: black;
  border-radius: 14px;
  cursor: pointer;
  font-size: 18px;
  line-height: 28px;
}

.main-heading {
  font-size: 40px;
  line-height: 60px;
  color: black;
  font-weight: 400;
  background-color: #B9FF66;
  width: fit-content;
  padding: 0 7px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 7px;
}

.main-description {
  padding-bottom: 40px;
}

.page-header {
  padding: 30px 0;
}
@media (min-width: 1440px) {
  .page-header {
    padding: 60px 0 70px 0;
  }
}

.main-nav {
  position: relative;
}
@media (min-width: 1440px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: auto;
  }
}

.main-nav__logo {
  width: 144px;
  height: 23.61px;
}
@media (min-width: 1440px) {
  .main-nav__logo {
    width: 219.54px;
    height: 36px;
  }
}

.main-nav__list {
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1440px) {
  .main-nav__list {
    display: flex;
    width: 100%;
  }
}

.main-nav__toogle {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: 5px;
  right: 0;
  z-index: 2;
  border: 0;
  cursor: pointer;
  color: black;
  background-color: transparent;
}
@media (min-width: 1440px) {
  .main-nav__toogle {
    display: none;
  }
}

@media (max-width: 1439px) {
  .main-nav--opened {
    padding-top: 13px;
  }
}

.main-nav--opened .main-nav__toogle::before,
.main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 27px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: currentColor;
}

.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}

.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}

.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: black;
  box-shadow: 0 8px 0 0 #000, 0 16px 0 0 #000;
}

@media (max-width: ${mobile-width-max}) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}
@media (min-width: ${desktop-width}) {
  .site-list {
    flex-direction: row;
    align-items: center;
  }
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
  padding: 5px 0;
}
@media (min-width: ${desktop-width}) {
  .site-list__item--link {
    padding: 20px 35px;
    border: 1px solid black;
    border-radius: 14px;
  }
}

.partners {
  padding: 60px 0;
}
.partners__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  row-gap: 25px;
  column-gap: 37px;
}